import React from 'react';
import Layout from '@/components/layout'; // Import your layout component
import { FaUser, FaLock } from 'react-icons/fa'; // Import icons

export default function AccountPage() {
  return (
    <Layout>
      <div className="min-h-screen flex flex-col items-center justify-start bg-[#e9dccb] text-white p-8 relative">
        {/* Top Right Corner Icons and Logout */}
        <div className="absolute top-4 right-4 flex items-center space-x-4">
          <FaUser className="text-[#3F6565] text-lg" />
          <button className="text-[#3F6565] font-semibold">My Account</button>
          <FaLock className="text-[#3F6565] text-lg" />
          <button className="text-[#3F6565] font-semibold">Logout</button>
        </div>

        {/* Optional: Add more content here */}
      </div>
    </Layout>
  );
}